// 
import Nav from './nav.json'
// 
import homeNav from './homeNav.json'
// top250
import top250 from './top250.json'
// 
import design from './design.json'
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

let store = new Vuex.Store({
  // 
  state: {
    title: '',
    p: 0,
    Nav,
    homeNav,
    top250,
    design,
    url: '',
    designList: [],
    LeftNav: false,
    leftsonNav: 0,
    scroll: 60
  },
  mutations: {
    titleFn (store, vla) {
      store.title = vla
    },
    updateUrl (store, url) {
      // 
      store.url = url
    },
    LeftNavFn (store) {
      // 
      store.LeftNav = !store.LeftNav
    },
    nullList (store) {
      // 
      // 
      store.designList = []
    },
    designfn (store, data) {
      // 
      store.designList.push(data)
    },
    leftsongNavFn (store, p) {
      // 
      store.leftsonNav = p
    },
    scrollFn (store, num) {
      // 
      // 60px
      store.scroll = num
    }
  },
  getters: {
  },
  actions: {
  }
})
export default store



